import common from "../../common/common.js"
export default {
	data() {
		return {
			globalUser: ''
		}
	},
	methods: {
		formSubmitNickname(e) {
			let me = this
			let nickname = e.detail.value.nickname
		
			// 请求轮播图数据
			uni.request({
				url: me.serverUrl + "/user/modifyUserinfo" + "?qq=565580328",
				data: {
					"userId": me.globalUser.id,
					"nickname": nickname
				},
				header: {
					"headerUserId": me.globalUser.id,
					"headerUserToken": me.globalUser.userUniqueToken
				},
				method: "POST",
				success(res) {
					// console.log(res)
					var resData = res.data;
					// console.log(typeof(resData));
					if (resData.status == 200) {
						// 获得最新的用户数据
						var userInfo = resData.data;
						uni.setStorageSync("globalUser", userInfo);
						uni.navigateBack({
							delta: 1
						})
					} else if (resData.status == 502 || resData.status == 500) {
						uni.showToast({
							title: res.data.msg,
							image: "../../static/icos/error.png",
							duration: 2000
						})
					}
				}
			})	
		}
	},
	onLoad() {
		let me = this
		let globalUser = me.getGlobalUser('globalUser')
		this.globalUser = globalUser
	}
}
